import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';

const messageVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { 
    opacity: 1, 
    y: 0,
    transition: { duration: 0.3, ease: "easeOut" as const }
  }
};

interface TypingMessageProps {
  content: string;
}

export const TypingMessage: React.FC<TypingMessageProps> = ({ content }) => {
  const [displayedContent, setDisplayedContent] = useState('');

  useEffect(() => {
    let i = 0;
    const typingInterval = setInterval(() => {
      if (i < content.length) {
        setDisplayedContent(content.substring(0, i + 1));
        i++;
      } else {
        clearInterval(typingInterval);
      }
    }, 20); // Adjust typing speed

    return () => clearInterval(typingInterval);
  }, [content]);

  return (
    <motion.div
      variants={messageVariants}
      className="prose dark:prose-invert max-w-none"
    >
      {displayedContent.split('\n').map((line, i) => (
        <p key={i} className="mb-2 last:mb-0">
          {line}
        </p>
      ))}
      {displayedContent.length < content.length && (
        <motion.span
          animate={{ opacity: [0.2, 1, 0.2] }}
          transition={{ repeat: Infinity, duration: 1 }}
          className="inline-block w-2 h-4 bg-blue-500 ml-1"
        />
      )}
    </motion.div>
  );
}; 