import axios, {  InternalAxiosRequestConfig } from 'axios';

const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:5000';

// Types
export interface User {
  id: string;
  name: string;
  email: string;
}

export interface AuthResponse {
  success: boolean;
  token: string;
  user: User;
}

export interface Message {
  id: string;
  content: string;
  sender: 'user' | 'assistant';
  timestamp: string;
}

export interface Chat {
  id: string;
  title: string;
  messages: Message[];
  createdAt: string;
  updatedAt: string;
}

export interface ChatResponse {
  success: boolean;
  data: {
    chatId: string;
    messages: Message[];
  };
}

export interface ChatHistoryResponse {
  success: boolean;
  data: Chat[];
}

// Create axios instance with default config
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Add request interceptor for authentication
api.interceptors.request.use((config: InternalAxiosRequestConfig) => {
  const token = localStorage.getItem('token');
  if (token && config.headers) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

// Add response interceptor for error handling
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Auth API
export const auth = {
  login: async (email: string, password: string) => {
    const response = await api.post('/api/auth/login', { email, password });
    return response.data;
  },

  register: async (name: string, email: string, password: string) => {
    const response = await api.post('/api/auth/register', { name, email, password });
    return response.data;
  },

  getCurrentUser: async () => {
    const response = await api.get('/api/auth/me');
    return response.data;
  },
};

// Chat API
export const chatAPI = {
  getChatHistory: async (): Promise<ChatHistoryResponse> => {
    const response = await api.get<ChatHistoryResponse>('/auth/chatHistory');
    return response.data;
  },

  sendMessage: async (data: { message: string; chatId?: string }): Promise<ChatResponse> => {
    const response = await api.post<ChatResponse>('/auth/chat', data);
    return response.data;
  },
};

export const chat = {
  getChats: async () => {
    const response = await api.get('/api/chats');
    return response.data;
  },

  getChat: async (chatId: string) => {
    const response = await api.get(`/api/chats/${chatId}`);
    return response.data;
  },

  createChat: async (title: string) => {
    const response = await api.post('/api/chats', { title });
    return response.data;
  },

  sendMessage: async (chatId: string, content: string) => {
    const response = await api.post(`/api/chats/${chatId}/messages`, { content });
    return response.data;
  },

  clearChat: async (chatId: string) => {
    const response = await api.post(`/api/chats/${chatId}/clear`);
    return response.data;
  },
};

export default api; 