import React, { useState, useEffect } from 'react';
import { 
   
  MessageSquare, 
  
  Trash2, 
  ChevronLeft, 
  ChevronRight,
  Clock,
  Search,
  
  Plus,
  X,
  Menu
} from 'lucide-react';
import type { ChatMessage } from '../../types';
import type { Chat } from '../../services/api';
import { Button } from '../ui/Button';
import { Input } from '../ui/Input';
import { motion, AnimatePresence } from 'framer-motion';
import { useHotkeys } from 'react-hotkeys-hook';

interface SidebarProps {
  messages: ChatMessage[];
  inputHistory: string[];
  onSelectHistoryItem: (item: string) => void;
  onExportChat: () => void;
  onClearMessages: () => void;
  onClearHistory: () => void;
  chatSessions: Chat[];
  currentChatId?: string;
  onSelectChat: (chatId: string) => void;
  onNewChat: () => void;
}

export const Sidebar: React.FC<SidebarProps> = ({
  inputHistory,
  onSelectHistoryItem,
 
  onClearMessages,
  onClearHistory,
  chatSessions,
  currentChatId,
  onSelectChat,
  onNewChat
}) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [showClearConfirmation, setShowClearConfirmation] = useState(false);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [isMobileOpen, setIsMobileOpen] = useState(false);

  // Toggle sidebar state
  const toggleSidebar = () => {
    if (window.innerWidth < 768) {
      setIsMobileOpen(!isMobileOpen);
    } else {
      setIsCollapsed(!isCollapsed);
    }
  };

  // Close mobile sidebar when resizing to desktop
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 768) {
        setIsMobileOpen(false);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Keyboard shortcut for toggling sidebar (Cmd/Ctrl + B)
  useHotkeys('ctrl+b, cmd+b', toggleSidebar);

  const filteredHistory = inputHistory.filter(item =>
    item.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleClearAllData = () => {
    setShowClearConfirmation(true);
  };

  const confirmClearAll = () => {
    onClearMessages();
    onClearHistory();
    setShowClearConfirmation(false);
  };

  // Collapsed sidebar content (icons only)
  const collapsedContent = (
    <div className="h-full flex flex-col items-center py-4 space-y-4">
      <button
        onClick={toggleSidebar}
        className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
      >
        <ChevronRight size={20} className="text-gray-600 dark:text-gray-300" />
      </button>
      
      <button
        onClick={onNewChat}
        className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
        title="New Chat"
      >
        <Plus size={20} className="text-gray-600 dark:text-gray-300" />
      </button>

      <div className="flex-1 flex flex-col items-center space-y-4 overflow-y-auto py-2">
        {chatSessions.slice(0, 8).map((chat) => (
          <button
            key={chat.id}
            onClick={() => {
              onSelectChat(chat.id);
              if (window.innerWidth < 768) setIsMobileOpen(false);
            }}
            className={`p-2 rounded-lg transition-colors ${
              chat.id === currentChatId
                ? 'bg-blue-100 dark:bg-blue-900/30'
                : 'hover:bg-gray-100 dark:hover:bg-gray-700'
            }`}
            title={chat.title}
          >
            <MessageSquare 
              size={18} 
              className={
                chat.id === currentChatId 
                  ? 'text-blue-600 dark:text-blue-400' 
                  : 'text-gray-600 dark:text-gray-300'
              } 
            />
          </button>
        ))}
      </div>

      
    </div>
  );

  // Expanded sidebar content
  const expandedContent = (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b border-gray-200 dark:border-gray-700 flex justify-between items-center">
        <h2 className="text-lg font-semibold text-gray-900 dark:text-white">Chat History</h2>
        <div className="flex items-center gap-2">
          <button
            onClick={onNewChat}
            className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
            title="New Chat"
          >
            <Plus size={18} className="text-gray-600 dark:text-gray-300" />
          </button>
          <button
            onClick={toggleSidebar}
            className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
          >
            <ChevronLeft size={18} className="text-gray-600 dark:text-gray-300" />
          </button>
        </div>
      </div>

      {/* Search */}
      <div className="p-4 border-b border-gray-200 dark:border-gray-700">
        <div className="relative">
          <Search size={16} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
          <Input
            type="text"
            placeholder="Search chats..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-9 w-full"
          />
          {searchQuery && (
            <button
              onClick={() => setSearchQuery('')}
              className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
            >
              <X size={16} />
            </button>
          )}
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto">
        {/* Chat Sessions */}
        <div className="p-4 space-y-1">
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2 flex items-center gap-2">
            <MessageSquare size={16} />
            Recent Chats
          </h3>
          {chatSessions.length === 0 ? (
            <p className="text-sm text-gray-500 dark:text-gray-400 p-2">No chats yet</p>
          ) : (
            chatSessions.map((chat) => (
              <motion.button
                key={chat.id}
                whileHover={{ scale: 1.01 }}
                onClick={() => {
                  onSelectChat(chat.id);
                  if (window.innerWidth < 768) setIsMobileOpen(false);
                }}
                className={`w-full p-2 text-left rounded-lg flex items-center gap-2 ${
                  chat.id === currentChatId
                    ? 'bg-blue-50 dark:bg-blue-900/20 text-blue-600 dark:text-blue-400'
                    : 'hover:bg-gray-100 dark:hover:bg-gray-700'
                }`}
              >
                <span className="truncate flex-1">{chat.title || 'Untitled Chat'}</span>
                <span className="text-xs text-gray-400">
                  {new Date(chat.createdAt).toLocaleDateString()}
                </span>
              </motion.button>
            ))
          )}
        </div>

        {/* Input History */}
        {inputHistory.length > 0 && (
          <div className="p-4 border-t border-gray-200 dark:border-gray-700">
            <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 mb-2 flex items-center gap-2">
              <Clock size={16} />
              Message History
            </h3>
            <div className="space-y-1">
              {filteredHistory.length === 0 ? (
                <p className="text-sm text-gray-500 dark:text-gray-400 p-2">No matching messages</p>
              ) : (
                filteredHistory.map((item, index) => (
                  <motion.button
                    key={index}
                    whileHover={{ scale: 1.01 }}
                    onClick={() => {
                      onSelectHistoryItem(item);
                      if (window.innerWidth < 768) setIsMobileOpen(false);
                    }}
                    className="w-full p-2 text-left text-sm text-gray-600 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg truncate"
                  >
                    {item}
                  </motion.button>
                ))
              )}
            </div>
          </div>
        )}
      </div>

      {/* Footer Actions */}
      <div className="p-4 border-t border-gray-200 dark:border-gray-700 space-y-2">
       

        <AnimatePresence>
          {showClearConfirmation ? (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              className="overflow-hidden bg-red-50 dark:bg-red-900/10 rounded-lg p-2 space-y-2"
            >
              <p className="text-sm text-red-600 dark:text-red-400">
                Clear all chat data?
              </p>
              <div className="flex gap-2">
                <Button
                  variant="primary"
                  size="sm"
                  className="flex-1 bg-red-600 hover:bg-red-700"
                  onClick={confirmClearAll}
                >
                  Clear All
                </Button>
                <Button
                  variant="ghost"
                  size="sm"
                  className="flex-1"
                  onClick={() => setShowClearConfirmation(false)}
                >
                  Cancel
                </Button>
              </div>
            </motion.div>
          ) : (
            <Button
              variant="ghost"
              className="w-full justify-start gap-2 text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300"
              onClick={handleClearAllData}
            >
              <Trash2 size={16} />
              Clear All Data
            </Button>
          )}
        </AnimatePresence>
      </div>
    </div>
  );

  return (
    <>
      {/* Mobile menu button (shown only on mobile) */}
      <button
        onClick={toggleSidebar}
        className="fixed top-4 left-4 z-50 p-2 rounded-lg bg-white dark:bg-gray-800 shadow-md lg:hidden"
      >
        <Menu size={24} className="text-gray-600 dark:text-gray-300" />
      </button>

      {/* Desktop Sidebar */}
      <div className="hidden lg:flex h-full">
        {isCollapsed ? (
          <motion.div
            initial={{ width: 0 }}
            animate={{ width: '4rem' }}
            exit={{ width: 0 }}
            transition={{ duration: 0.2 }}
            className="h-full bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700"
          >
            {collapsedContent}
          </motion.div>
        ) : (
          <motion.div
            initial={{ width: 0 }}
            animate={{ width: '18rem' }}
            exit={{ width: 0 }}
            transition={{ duration: 0.2 }}
            className="h-full bg-white dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700"
          >
            {expandedContent}
          </motion.div>
        )}
      </div>

      {/* Mobile Sidebar Overlay */}
      <AnimatePresence>
        {isMobileOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
              onClick={() => setIsMobileOpen(false)}
            />
            <motion.div
              initial={{ x: '-100%' }}
              animate={{ x: 0 }}
              exit={{ x: '-100%' }}
              transition={{ type: 'spring', stiffness: 300, damping: 30 }}
              className="fixed inset-y-0 left-0 w-64 z-50 bg-white dark:bg-gray-800 shadow-xl lg:hidden"
            >
              {expandedContent}
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  );
};