import type { ChatMessage } from '../types';

export const exportChatAsJSON = async (messages: ChatMessage[]): Promise<void> => {
  try {
    const chatData = {
      messages,
      exportDate: new Date().toISOString(),
      version: '1.0'
    };

    const blob = new Blob([JSON.stringify(chatData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    
    const a = document.createElement('a');
    a.href = url;
    a.download = `chat-export-${new Date().toISOString()}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error('Error in exportChatAsJSON:', error);
    throw error;
  }
};

export const exportChatAsText = (messages: ChatMessage[], filename?: string) => {
  const textContent = messages.map(msg => 
    `[${msg.timestamp.toLocaleString()}] ${msg.sender.toUpperCase()}: ${msg.content}`
  ).join('\n\n');
  
  const dataUri = 'data:text/plain;charset=utf-8,'+ encodeURIComponent(textContent);
  const exportFileDefaultName = filename || `chat-export-${new Date().toISOString().split('T')[0]}.txt`;
  
  const linkElement = document.createElement('a');
  linkElement.setAttribute('href', dataUri);
  linkElement.setAttribute('download', exportFileDefaultName);
  linkElement.click();
};