import React, { useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Bot, User } from 'lucide-react';
import { TypingMessage } from './TypingMessage';
import { ThinkingIndicator } from './ThinkingIndicator';
import { TypingIndicator } from './TypingIndicator';

interface Message {
  id: string;
  role: 'user' | 'assistant';
  content: string;
  timestamp: number;
  isThinking?: boolean;
}

interface MessageListProps {
  messages: Message[];
  isGenerating: boolean;
}

const messageVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0 },
  exit: { opacity: 0, y: -20 }
};

export const MessageList: React.FC<MessageListProps> = ({ messages, isGenerating }) => {
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, isGenerating]);

  return (
    <div className="space-y-4 p-4">
      <AnimatePresence>
        {messages.map((message) => (
          <motion.div
            key={message.timestamp}
            layout
            initial="hidden"
            animate="visible"
            exit="exit"
            variants={messageVariants}
            className={`
              flex items-start gap-3 p-4 rounded-lg
              ${message.role === 'user' 
                ? 'bg-blue-50/80 dark:bg-blue-900/20 backdrop-blur-sm justify-end' 
                : 'bg-gray-50/80 dark:bg-gray-800/50 backdrop-blur-sm justify-start'}
            `}
          >
            {message.role === 'assistant' && (
              <div className="flex-shrink-0 w-8 h-8 rounded-full bg-purple-500 flex items-center justify-center">
                <Bot className="w-5 h-5 text-white" />
              </div>
            )}
            
            <div className="flex-1 min-w-0">
              <div className="text-sm font-medium mb-1">
                {message.role === 'user' ? 'You' : 'Assistant'}
              </div>
              {message.isThinking ? (
                <ThinkingIndicator />
              ) : (
                <TypingMessage content={message.content} />
              )}
            </div>

            {message.role === 'user' && (
              <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center">
                <User  className="w-5 h-5 text-white" />
              </div>
            )}
          </motion.div>
        ))}
      </AnimatePresence>

      {/* Only show typing indicator if there are no thinking messages */}
      {isGenerating && !messages.some(m => m.isThinking) && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="flex items-center gap-3 p-4 bg-gray-50/80 dark:bg-gray-800/50 backdrop-blur-sm rounded-lg"
        >
          <div className="flex-shrink-0 w-8 h-8 rounded-full bg-purple-500 flex items-center justify-center">
            <Bot className="w-5 h-5 text-white" />
          </div>
          <TypingIndicator />
        </motion.div>
      )}
      <div ref={messagesEndRef} />
    </div>
  );
};