import React, { useState, useRef, useEffect } from 'react';
import { Send, Mic, MicOff,  History,  StopCircle, Sparkles } from 'lucide-react';
import { Button } from '../ui/Button';
import { useSpeechRecognition } from '../../hooks/useSpeechRecognition';
import { LoadingSpinner } from '../ui/LoadingSpinner';
import { motion, AnimatePresence } from 'framer-motion';
import { Tooltip } from '../ui/Tooltip';
import { useHotkeys } from 'react-hotkeys-hook';

interface MessageInputProps {
  onSendMessage: (message: string) => void;
  onNavigateHistory: (direction: 'up' | 'down') => string;
  onExportChat: () => void;
  disabled: boolean;
  inputHistory: string[];
  historyIndex: number;
  onResetHistoryIndex: () => void;
  onClearHistory: () => void;
  isGenerating: boolean;
}

export const MessageInput: React.FC<MessageInputProps> = ({
  onSendMessage,
  onNavigateHistory,
  
  disabled,
  inputHistory,

  onResetHistoryIndex,
  onClearHistory,
  isGenerating
}) => {
  const [message, setMessage] = useState('');
  const [showHistoryTooltip, setShowHistoryTooltip] = useState(false);
  const [showQuickPrompts, setShowQuickPrompts] = useState(true);
  const [isFocused, setIsFocused] = useState(false);
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  
  const {
    isListening,
    transcript,
    startListening,
    stopListening,
    isSupported: speechSupported,
    error: speechError
  } = useSpeechRecognition();

  // Keyboard shortcuts
  useHotkeys('enter', (e) => {
    e.preventDefault();
    handleSubmit();
  }, { preventDefault: true, enableOnFormTags: true });
  useHotkeys('shift+enter', () => {}, { preventDefault: true });
  useHotkeys('esc', () => {
    setMessage('');
    onResetHistoryIndex();
    textareaRef.current?.focus();
  });

  useEffect(() => {
    if (transcript) {
      setMessage(prev => prev + ' ' + transcript);
    }
  }, [transcript]);

  useEffect(() => {
    if (speechError) {
      console.error('Speech recognition error:', speechError);
    }
  }, [speechError]);

  useEffect(() => {
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
      textareaRef.current.style.height = `${Math.min(textareaRef.current.scrollHeight, 150)}px`;
    }
  }, [message]);

  // Hide quick prompts when typing or after using one
  useEffect(() => {
    if (message) {
      setShowQuickPrompts(false);
    }
  }, [message]);

  const handleSubmit = (e?: React.FormEvent) => {
    e?.preventDefault();
    if (message.trim() && !disabled) {
      onSendMessage(message.trim());
      setMessage('');
      onResetHistoryIndex();
      setShowQuickPrompts(false);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit();
    } else if (e.key === 'ArrowUp' && !message) {
      e.preventDefault();
      const historyMessage = onNavigateHistory('up');
      setMessage(historyMessage);
    } else if (e.key === 'ArrowDown' && !message) {
      e.preventDefault();
      const historyMessage = onNavigateHistory('down');
      setMessage(historyMessage);
    }
  };

  const toggleVoiceInput = () => {
    if (isListening) {
      stopListening();
    } else {
      startListening();
      setMessage('');
    }
  };

  const handleClearHistory = () => {
    if (window.confirm('Are you sure you want to clear your input history? This cannot be undone.')) {
      onClearHistory();
    }
  };

  const quickPrompts = [
    "What can you help me with?",
    "How does this work?",
    "Tell me more about yourself",
    "What are your capabilities?",
    "Suggest some conversation starters",
    "Explain like I'm 5 years old"
  ];

  const handleQuickPrompt = (prompt: string) => {
    setMessage(prompt);
    setShowQuickPrompts(false);
    textareaRef.current?.focus();
  };

  return (
    <div className="relative px-2">
      <AnimatePresence>
        {showQuickPrompts && !message && !isGenerating && (
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            transition={{ duration: 0.2 }}
            className="mb-4"
          >
            <div className="flex items-center justify-between mb-2">
              <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-1">
                <Sparkles className="w-4 h-4" />
                Quick Prompts
              </h3>
              <button
                onClick={() => setShowQuickPrompts(false)}
                className="text-xs text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
              >
                Hide
              </button>
            </div>
            <div className="flex flex-wrap gap-2">
              {quickPrompts.map((prompt, index) => (
                <motion.button
                  key={index}
                  whileHover={{ scale: 1.03 }}
                  whileTap={{ scale: 0.98 }}
                  onClick={() => handleQuickPrompt(prompt)}
                  className="px-3 py-1.5 text-sm bg-gray-100 dark:bg-gray-800 text-gray-700 dark:text-gray-300 rounded-full hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors"
                >
                  {prompt}
                </motion.button>
              ))}
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      <form onSubmit={handleSubmit} className="relative">
        <div className="flex items-end gap-2">
          <div className="flex-1 relative">
            <motion.div
              animate={{
                borderColor: isFocused ? 'rgba(59, 130, 246, 0.5)' : 'rgba(209, 213, 219, 0.5)',
                boxShadow: isFocused ? '0 0 0 2px rgba(59, 130, 246, 0.2)' : 'none'
              }}
              transition={{ duration: 0.2 }}
              className="rounded-lg border dark:border-gray-700"
            >
              <textarea
                ref={textareaRef}
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                onKeyDown={handleKeyDown}
                onFocus={() => setIsFocused(true)}
                onBlur={() => setIsFocused(false)}
                placeholder="Type your message..."
                className="w-full min-h-[44px] max-h-[150px] p-3 pr-12 rounded-lg bg-white dark:bg-gray-800 text-gray-900 dark:text-gray-100 resize-none focus:outline-none focus:ring-0 border-none"
                disabled={disabled}
                rows={1}
              />
            </motion.div>

            <div className="absolute right-2 bottom-2 flex gap-1">
              {speechSupported && (
                <Tooltip content={isListening ? "Stop listening" : "Voice input"}>
                  <button
                    type="button"
                    onClick={toggleVoiceInput}
                    disabled={disabled}
                    className={`p-1.5 rounded-full ${isListening ? 'bg-red-100 text-red-600 dark:bg-red-900/30 dark:text-red-400' : 'text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200'} transition-colors`}
                  >
                    {isListening ? (
                      <motion.div
                        animate={{ scale: [1, 1.1, 1] }}
                        transition={{ repeat: Infinity, duration: 1 }}
                      >
                        <MicOff size={18} />
                      </motion.div>
                    ) : (
                      <Mic size={18} />
                    )}
                  </button>
                </Tooltip>
              )}

              {inputHistory.length > 0 && (
                <Tooltip content="History">
                  <button
                    type="button"
                    onClick={() => setShowHistoryTooltip(!showHistoryTooltip)}
                    className="p-1.5 rounded-full text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-200 transition-colors"
                  >
                    <History size={18} />
                  </button>
                </Tooltip>
              )}
            </div>

            {showHistoryTooltip && (
              <div className="absolute bottom-14 right-0 w-64 bg-white dark:bg-gray-800 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700 z-10 p-2">
                <div className="flex justify-between items-center mb-2 px-2">
                  <span className="text-sm font-medium">History</span>
                  <button
                    onClick={handleClearHistory}
                    className="text-xs text-red-500 hover:text-red-700 dark:hover:text-red-400"
                  >
                    Clear
                  </button>
                </div>
                <div className="max-h-60 overflow-y-auto">
                  {inputHistory.map((item, index) => (
                    <button
                      key={index}
                      onClick={() => {
                        setMessage(item);
                        setShowHistoryTooltip(false);
                        textareaRef.current?.focus();
                      }}
                      className="w-full text-left p-2 text-sm hover:bg-gray-100 dark:hover:bg-gray-700 rounded truncate"
                    >
                      {item}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {disabled && (
              <div className="absolute inset-0 flex items-center justify-center bg-white/50 dark:bg-gray-800/50 rounded-lg backdrop-blur-sm">
                <LoadingSpinner size="sm" className="text-gray-400" />
              </div>
            )}
          </div>

          <motion.div
            whileHover={!disabled && message.trim() ? { scale: 1.05 } : {}}
            whileTap={!disabled && message.trim() ? { scale: 0.95 } : {}}
          >
            <Button
              type="submit"
              disabled={!message.trim() || disabled}
              className="h-[44px] px-4 min-w-[100px]"
            >
              {isGenerating ? (
                <>
                  <StopCircle className="w-5 h-5 mr-2" />
                  Stop
                </>
              ) : (
                <>
                  <Send className="w-5 h-5 mr-2" />
                  Send
                </>
              )}
            </Button>
          </motion.div>
        </div>
      </form>

      <div className="flex justify-between items-center mt-2 px-2">
        <div className="text-xs text-gray-500 dark:text-gray-400">
          {isListening ? (
            <motion.div
              animate={{ opacity: [0.6, 1, 0.6] }}
              transition={{ repeat: Infinity, duration: 1.5 }}
              className="flex items-center gap-1"
            >
              <span className="relative flex h-2 w-2">
                <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-red-400 opacity-75"></span>
                <span className="relative inline-flex rounded-full h-2 w-2 bg-red-500"></span>
              </span>
              Listening...
            </motion.div>
          ) : (
            <span>Press Shift+Enter for new line</span>
          )}
        </div>
        <div className="text-xs text-gray-500 dark:text-gray-400">
          {message.length}/2000
        </div>
      </div>
    </div>
  );
};
